.tooltip-error {
  position: absolute;
  top: 80%;

  z-index: 1;
  max-width: 100%;
  display: none;
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
  line-height: 1.5;
  color: #fff;
  background-color: rgba(220, 53, 69, 0.4);
  border-radius: 0.25rem;
}

.active-tooltip {
  display: block;
}
