@import url(https://fonts.googleapis.com/css?family=Nunito:300,400,600,700&display=swap);
.container {
  background-color: #202020;
  padding: 45px;
}

.gap-5 {
  grid-gap: 5px !important;
  gap: 5px !important;
  margin-bottom: 5px;
  color: #fff;
}

.currencyHighLight {
  color: #ffd500;
  font-size: 3rem;
  font-weight: 600;
}

.currency {
  color: #ffd500;
  font-size: 1rem;
  font-weight: bold;
}

.title {
  color: #fff !important;
  font-size: 1.8rem;
  font-weight: 600;
}

.containerSubsSells {
  padding: 2vw;
  display: flex;
  width: 70%;
  justify-content: space-between;
  margin-bottom: 45px;
}

.containerShows {
  padding: 2vw;
  display: flex;
  width: 70%;
  justify-content: space-between;
  margin-bottom: 45px;
}

.subtitle {
  color: wheat;
  margin: 10px;
}
.card{
  position: relative;
}
.overlay {
  position: absolute;
  bottom: 0;
  top: 0;
  background: rgb(0,0,0);
  background: linear-gradient(180deg, rgba(0,0,0,1) 0%, rgba(255,255,255,0) 100%);
  transition: 0.5s ease;
  width: 100%;
  text-align: center;
  font-weight: bold;
  color: #fff;
  padding: 20px;
  opacity: 0;
}

.card:hover .overlay{
  cursor: pointer;
  opacity: 1;
}
.card img{
  transition: all .5s ease;
}

.card:hover img{
  width: 200px !important;
  height: 250px !important;
}

.img-fit{
  object-fit: cover;
  height: 270px;
  max-width: 100%;
  width: 100%;
  object-position: 10% 5%;
}

.list-shows{
  opacity: 0;
  transition: all .6s ease;
}

.open{
  opacity: 1;
}

