/* Auth */

.auth {
  height: 100vh;
  .login-half-bg {
    // background: url("../../../images/auth/login-bg.jpg");
    background-size: cover;
  }
  .register-half-bg {
    // background: url("../../../images/auth/register-bg.jpg");
    background-size: cover;
  }
  &.lock-full-bg {
    background: url('../../../assets/images/background-register.png');
    background-size: cover;
    background-position: center center;
  }
  .lock-profile-img {
    width: 90px;
    height: 90px;
    border-radius: 100%;
  }
  .auth-form-light {
    background: $white;
    select {
      color: $input-placeholder-color;
    }
    .input-group {
      .form-control {
        :focus {
          border-color: $border-color !important;
          background: $white !important;
          outline: transparent !important;
          box-shadow: 0 0 5px rgb(94, 94, 94) !important;
        }
        :active {
          border-color: $border-color;
          background: $white;
          outline: transparent !important;
        }
      }
    }
  }
  .auth-form-transparent {
    background: theme-color(black-alpha);
    margin-top: 30px;
    .form-control,
    .input-group-text {
      border-color: theme-color(secondary);
      &:focus,
      &:active {
        border-color: theme-color(secondary);
      }
    }
    select {
      outline-color: theme-color(secondary);
    }
  }
  &.auth-img-bg {
    padding: 0;
    .auth-form-transparent {
      @media (min-width: 768px) {
        margin: auto;
        margin-top: 30px;
      }
    }
  }
  .brand-logo {
    margin-bottom: 2rem;
    img {
      width: 150px;
    }
  }
  form {
    .form-group {
      margin-bottom: 1.5rem;
      label {
        font-size: 0.8125rem;
      }
      .form-control {
        background: transparent;
        border-radius: 0;
        font-size: 0.9375rem;
      }
    }
    .auth-form-btn {
      padding: 1rem 3rem;
      line-height: 1.5;
    }
    .auth-link {
      font-size: $default-font-size;
      &:hover {
        color: initial;
      }
    }
  }
}
